$front-color: #081c15;//#000000;
$back-color:  #fefae0;//#14213d;
$front-color: #fefae0;//#000000;
$back-color:   rgb(36, 45, 95);//#14213d;
$content-color: #f4f1de;// #e5e5e5;
$content-color-font: black;
$side-size: 84px;

.App {
  text-align: center;
}

body {
  background: $back-color; 
  
}

.mainBody {
  background: $back-color;
  color:   $front-color;
  height: 100%;
  width: 100%;
}

.left {
  width: calc(25%);
  height: 100%;
  //padding-left: 2%;
  padding-right: 40px;
  float: left;
  text-align: right;
  position: fixed;
  border-right: 0.5px solid rgba(255,255,255,.20);
  background: $back-color;
  transition: all .35s cubic-bezier(.07,.65,.36,.92);
}

.leftClose {
  width: calc(25%);
  height: 100%;
  margin-left: calc(-25%);
  //padding-left: 2%;
  //left: calc(-25%);
  padding-right: 40px;
  float: left;
  text-align: right;
  position: fixed;
  border-right: 0.5px solid rgba(255,255,255,.20);
  background: $back-color;
  transition: all .35s cubic-bezier(.07,.65,.36,.92);
}

.right {
  background: $back-color;
  //background: rgba(0,0,0,0.5);
  width: calc(75% - 40px);
  //height: 100px;
  text-align: left;
  float: left;
  //margin-top: 40px;
  padding-left: calc(25% + 40px);
  margin-left: 0px;
  //padding-right: calc(50% - 740px);
  transition: all .35s cubic-bezier(.07,.65,.36,.92);
  font-family: 'Overpass', sans-serif;
  h2 {
    margin-left: 30px;
    margin-right: 30px;
    color: rgb(151,213,162);
  }
  h2:hover {
    color: rgba(151,213,162,0.7);
  }
  h3, h4, h5 {
    margin-left: 30px;
    margin-right: 30px;
    color: rgb(232,177,174);
    //padding-right: calc(60% - 480px);
  }
  a {
    text-decoration: none;
  }
  a:hover {
    text-decoration: none;
  }
  p {
    margin-left: 30px;
    margin-right: 30px;
    text-decoration: none;
    color: white;
  }
  hr {
    margin-left: 30px;
    margin-right: 30px;
    text-decoration: none;
    color: white;
    height: 0.5px;
    border: none;
    background-color: white;
    appearance: none;
  }
  .listEnt {
    border-left: 0.5px solid rgb(247,216,94);
    transition: all .35s cubic-bezier(.07,.65,.36,.92);
  }
  .listEnt:hover {
    h3, p {
      color: rgb(247,216,94);
      text-decoration: underline;
    }
  }
  .tab {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    table {
      //margin-left: (9%);
      font-size: 14px;
      border: 1px solid rgb(204,209,221);
      border-collapse: collapse;
      //color: rgb(204,209,221);
    }
    th {
      text-align: center;
      border: 1px solid rgb(204,209,221);
    }
    td {
      padding-left: 7px;
      text-align: left;
      border: 1px solid rgb(204,209,221);
    }
  }
  .fig {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    font-size: 14px;
    img {
      margin-left: auto;
      margin-right: auto;
      padding-top: 20px;
      //margin-left: 13%;
      width: 100%;
    }
  }
  .rightHead {
    background: rgba(36, 45, 95, 0.8);
    width: calc(75% - 26px);
    margin-left: 1px;
    //margin-right: 50px;
    //padding-top: 40px;
    position: fixed;
    height: 100px;
    line-height: 60px;
    //opacity: 0.4;
    z-index: 99;
    border-bottom: 0.5px solid rgba(255,255,255,.20);
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    //h2 {
    //  width: 570px;
    //  padding-left: 20px;
    //  margin-left: auto;
    //  margin-right: auto;
    //}
    .burgerCont {
      margin-left: auto;
      margin-right: auto;
      width: 630px;
      //padding-left: 30px;
     // padding-right: 30px;
      .bur {
        display: inline-block;
        width: calc(566px);
        height: 100%;
        h2 {
          //width: 500px;
          padding-left: 20px;
          margin-left: 0;
          margin-right: 0;
        }
      }
      .ger {
        display: inline-block;
        //padding-top: 17px;
        //width: 24px;
        //float: right;
        padding-right: 0;
      }
    }
  }
  .rightContent {
    padding-top: 110px;
    padding-bottom: 50px;
    width: calc(630px);
    text-align: justify;
    //right: 0;
    margin-left: auto;
    margin-right: auto;
  }
}

