.but {
  padding-top: 8px;
  padding-left: 30px;
  padding-bottom:35px;
  
  a {
    float: left;
    text-decoration: none;
    //padding: 30px;
    margin-right: 10px;
    //color: $back-color;
    color: rgb(151,213,162);
    padding-left: 7px;
    padding-right: 7px;
    border-radius: 10px;
    transition: all .35s cubic-bezier(.07,.65,.36,.92);
  }
  a:hover {
    float: left;
    text-decoration: none;
    //padding: 30px;
    margin-right: 10px;
    color: $back-color;
    background-color: rgb(151,213,162);
    padding-left: 7px;
    padding-right: 7px;
    border-radius: 10px;
  }
}

.rightHome {
  margin-left:13%;
  padding-top:100px;
}

.container {
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  //font-family: "Open Sans", arial;
  //position: absolute;
  overflow-y: scroll;
}

.top {
  font-size: 0.75em;
  padding: 10px;
  //display: none;
  //height: 50px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  
  ul, li {
    list-style-type: none;
    //outline: 0;
    margin: 0;
    padding: 1;
    float: left;
    //text-transform: uppercase;
    transition: all .35s cubic-bezier(.07,.65,.36,.92);
  }
  span {
    padding-left: 14px;
    padding-right: 14px;
  }
  a {
    color: $front-color;
  }
  li {
    height: 50px;
    line-height: 50px;
  }
}


.leftImage {
  //width: 80%;
  width: calc(100%);
  //float: right;
  height: 299px;
  padding-top: 70px;
  padding-bottom: 55px;
  //float: right;
  text-align: center;
  
  //border: 2px solid #81b29a;
  display: block;
  box-sizing: border-box;
  img {
    //display: block;
    left: -10px;
    margin-left: auto;
    margin-right: auto;
    width: 184px;
    float: right;
    border-radius: 50%;
    border: 2px solid rgba(151,213,162,0.8);
  }
}

.leftContent {
  //padding-top: 30px;
  height: 500px;
  float: right;
  width: 200px;
  font-size: 16px;
  //font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  //  monospace;
  font-family: 'Montserrat', sans-serif;
  text-transform: lowercase;
  
  h4 {
    float: right;
    width: 200px;
    text-align: center;
  }
  ul {
    //width: 70px;
    list-style-type: none;
    //outline: 0;
    //margin: 0;
    width: 184px;
    //padding-left: 0;
    //padding-right: 15px;
    float: right;
    text-align: left;
    font-size: 14px;
    //text-transform: uppercase;
    transition: all .35s cubic-bezier(.07,.65,.36,.92);
  }
  li {
    //margin-top: 5px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 5px;
    padding-right: 5px;
    color: rgb(172,204,250);
    transition: all .35s cubic-bezier(.07,.65,.36,.92);
    //border-left: 0.5px solid rgb(247,216,94);
    height: 30px;
    hr {
      margin-left: 5px;
      margin-right: 30px;
      margin-bottom: 0px;
      text-decoration: none;
      color: white;
      height: 2px;
      border: none;
      background-color: white;
      appearance: none;
      transition: all .35s cubic-bezier(.07,.65,.36,.92);
    }
    .entry {
      padding-left: 10px;
      height: 30px;
      border-left: 0.5px solid rgb(247,216,94);
    }
  }
  //li:hover {
  //  background-color:  rgba(151,213,162,0.8);
  //  border-radius: 10px;
  //  a {
  //    color: $back-color;
  //  }
    
  //}
  a {
    //text-transform: uppercase;
    padding-left: 5px;
    padding-right: 5px;
    text-decoration: none;
    color: rgb(151,213,162);
    transition: all .35s cubic-bezier(.07,.65,.36,.92);
    font-weight: 500;
  }

  a:hover {
    text-decoration: none;
    //box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    //color: $back-color;
    font-weight: bold;
  }
}

.algo {
  margin-left: auto;
  margin-right: auto;
  width: 430px;
  thead {
    border: 1px solid rgb(204,209,221);
  }
  table {
    //margin-left: (9%);
    font-size: 14px;
    border: 1px solid rgb(204,209,221);
    border-collapse: collapse;
    width:430px;
    //color: rgb(204,209,221);
  }
  th {
    text-align: center;
    border: 1px solid rgb(204,209,221);
  }
  td {
    padding-left: 7px;
    text-align: left;
    border: none;
  }
}

@media screen and (max-width: 938px) {
  .left {
    display: none;
  }
  .right {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-left: calc(0px);
    //img{
    //  width: 70%;
    //}
    .rightHead {
      width: calc(100%);
      h2 {
        width: calc(630px);
        margin-left: auto;
        margin-right: auto;
        padding-left: 60px;
      }
    }
    .rightContent{
      width: calc(630px);
      margin-left: auto;
      margin-right: auto;
    }
  }
}

@media screen and (max-width: 630px) {
  .left {
    display: none;
  }
  .right {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-left: calc(0px);
    //img{
    //  width: 70%;
    //}
    .rightHead {
      width: calc(100%);
      h2 {
        width: calc(100% - 60px);
        margin-left: auto;
        margin-right: auto;
        padding-left: 0;
      }
    }
    .rightContent{
      width: calc(100%);
      margin-left: auto;
      margin-right: auto;
    }
  }

  .bur {
    //display: inline-block;
    margin-top: 35px;
    //margin-left: 200px;
    float: right;
    //padding-right: calc(auto -50px);
    //margin-right: 0px;
  }
}